/* App Shell Styles with Tailwind-like classes */
.app-shell {
	font-family: "Inter", sans-serif;
	background: #f9fafb;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	color: #1f2937;
}

.app-shell-header {
	background: white;
	padding: 1rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid #e5e7eb;
	box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}

.app-shell-logo {
	display: flex;
	align-items: center;
	gap: 0.5rem;
	font-weight: 600;
	font-size: 1.25rem;
}

.app-shell-logo-icon {
	width: 32px;
	height: 32px;
	background: #46777f;
	border-radius: 8px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.app-shell-content {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 2rem;
	text-align: center;
}

.app-shell-loading {
	margin: 2rem 0;
}

.app-shell-spinner {
	width: 48px;
	height: 48px;
	border: 4px solid #e5e7eb;
	border-top: 4px solid #46777f;
	border-radius: 50%;
	animation: spin 1s linear infinite;
	margin: 0 auto 1rem;
}

.app-shell-skeleton {
	background: #e5e7eb;
	border-radius: 8px;
	margin: 0.5rem 0;
	animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.app-shell-skeleton-header {
	height: 20px;
	width: 200px;
	margin: 0 auto 1rem;
}

.app-shell-skeleton-text {
	height: 16px;
	width: 150px;
	margin: 0.5rem auto;
}

.app-shell-footer {
	background: white;
	padding: 1rem;
	text-align: center;
	font-size: 0.875rem;
	color: #6b7280;
	border-top: 1px solid #e5e7eb;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@keyframes pulse {
	0%,
	100% {
		opacity: 1;
	}
	50% {
		opacity: 0.5;
	}
}

/* Mobile responsive */
@media (max-width: 768px) {
	.app-shell-content {
		padding: 1rem;
	}

	.app-shell-logo {
		font-size: 1rem;
	}

	.app-shell-logo-icon {
		width: 28px;
		height: 28px;
	}
}
